@import '../variables';
.first-screen {
	&__wrapper {
		position: relative;
		padding: 270px 0;
		padding-bottom: 118px;
		& img:first-child[data-img='bg'] {
			pointer-events: none;
			position: absolute;
			bottom: -85px;
			left: 50%;
			transform: translateX(-50%) translateX(-61px);
			width: 2440px;
			height: 1180px;
		}
	}
	& .container {
		position: relative;
		z-index: 1;
	}
	&__title {
		margin-bottom: 18px;
	}
	&__subtitle {
		margin-bottom: 40px;
	}
	&__note {
		padding: 32px 49px;
		border-radius: 10px;
		background: $transparent-bg-1;
		backdrop-filter: blur(6px);
		max-width: 852px;
		margin: 0 auto;
		text-align: center;
		margin-bottom: 32px;
		& p {
			margin: 0;
			color: $white;
			font-size: 1.6rem;
			line-height: calc(24 / 16);
		}
	}
	&__claim {
		display: grid;
		place-items: center;
		&-btn {
			background: radial-gradient(75% 75% at 50% 50%, #ffc902 0%, #ff8536 100%);
			padding: 1em 2.25em;
		}
	}
	&__scroll {
		display: flex;
		flex-direction: column;
		gap: 18px;
		align-items: center;
		&-wrapper {
			margin-top: 131px;
		}
		& span {
			font-size: 1.6rem;
			line-height: calc(24 / 16);
		}
	}
}

@media screen and (max-width: 1440px) {
	.first-screen {
		&__wrapper {
			padding-top: 240px;
			padding-bottom: 276px;
			& img:first-child[data-img='bg'] {
				width: 1861px;
				height: 900px;
				bottom: -44px;
			}
		}
		&__title {
			margin-bottom: 16px;
		}
		&__subtitle {
			margin-bottom: 32px;
		}
		&__note {
			max-width: 660px;
			padding: 24px;
			& p {
				font-size: 1.4rem;
				line-height: calc(22 / 14);
			}
		}
		&__scroll {
			&-wrapper {
				display: none;
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.first-screen {
		&__title {
			margin-bottom: 8px;
		}
		&__subtitle {
			margin-bottom: 20px;
		}
		&__note {
			padding: 20px;
			max-width: 400px;
			margin-bottom: 24px;
			& p {
				font-size: 1.2rem;
				line-height: calc(18 / 12);
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.first-screen {
		&__wrapper {
			padding-top: 98px;
			padding-bottom: 211px;
			& img:first-child[data-img='bg'] {
				width: 1450px;
				height: 713px;
				bottom: -40px;
				left: 50%;
				transform: translateX(-50%) translateX(123px);
			}
		}
	}
}
