@import '../variables';
.road-map {
	&__wrapper {
		padding-top: 144px;
		position: relative;
		overflow: hidden;
		& img[data-img='bg'] {
			position: absolute;
			top: 0;
			left: 50%;
			transform: translateX(-50%) translateX(-85px);
			width: 3569px;
			height: 1726px;
		}
	}
	&__dark {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background: rgba($dark-700, 0.75);
	}
	& .container {
		position: relative;
		z-index: 1;
	}
	&__road {
		& svg[data-img='road-1440'],
		& svg[data-img='road-1200'],
		& svg[data-img='road-576'] {
			display: none;
		}
		& svg[data-img='road'] {
			max-width: 100%;
			height: auto;
		}
		& svg[data-img='road-1440'] path,
		& svg[data-img='road'] path {
			box-shadow: 0px 0px 100px #fff;
		}
		&-wrapper {
			padding-top: 180px;
			padding-bottom: 396px;
			position: relative;
		}
	}
	&__item {
		position: absolute;
		display: flex;
		flex-direction: column;
		max-width: 366px;
		& p {
			max-width: 328px;
			margin: 0;
			font-size: 1.6rem;
			line-height: calc(24 / 16);
		}
		&-img {
			margin: 24px 0;
		}
		&-title {
			margin-bottom: 10px;
			& + p {
				max-width: 100%;
				margin-bottom: 16px;
			}
		}
		&[data-item='7'] .road-map__item-title + p,
		&[data-item='5'] .road-map__item-title + p {
			max-width: 273px;
		}
		&[data-item='4'] .road-map__item-title + p {
			max-width: 319px;
		}
		&[data-item='1'] {
			left: -5px;
			top: -12px;
		}
		&[data-item='2'] {
			left: 50%;
			top: 243px;
			transform: translateX(-50%) translateX(-45px);
		}
		&[data-item='3'] {
			left: 50%;
			top: 310px;
			transform: translateX(-50%) translateX(617px);
		}
		&[data-item='4'] {
			left: 50%;
			bottom: 321px;
			transform: translateX(-50%) translateX(617px);
		}
		&[data-item='5'] {
			left: 50%;
			bottom: 469px;
			transform: translateX(-50%) translateX(100px);
			& img {
				filter: drop-shadow(0px 0px 25px rgba(91, 235, 243, 0.3));
			}
		}
		&[data-item='6'] {
			left: 50%;
			bottom: 345px;
			transform: translateX(-50%) translateX(-386px);
			& img {
				filter: drop-shadow(0px 0px 25px $dark-700);
			}
		}
		&[data-item='7'] {
			left: -15px;
			bottom: 154px;
		}
	}
}

@media screen and (max-width: 1720px) {
	.road-map {
		&__item {
			&[data-item='1'] {
				left: 0;
			}
			&[data-item='3'] {
				transform: translateX(-50%) translateX(580px);
			}
			&[data-item='7'] {
				bottom: 160px;
				left: 0;
			}
		}
	}
}

@media screen and (max-width: 1660px) {
	.road-map {
		&__item {
			&[data-item='2'] {
				top: 220px;
			}
			&[data-item='5'] {
				bottom: 450px;
			}
			&[data-item='6'] {
				bottom: 320px;
				transform: translateX(-50%) translateX(-310px);
			}
		}
	}
}

@media screen and (max-width: 1590px) {
	.road-map {
		&__item {
			&[data-item='3'] {
				transform: none;
				right: 30px;
				left: auto;
			}
		}
	}
}

@media screen and (max-width: 1500px) {
	.road-map {
		&__item {
			&[data-item='2'] {
				transform: translateX(-50%) translateX(-155px);
			}
			&[data-item='4'] {
				transform: translateX(-50%) translateX(540px);
				bottom: 300px;
			}
			&[data-item='5'] {
				bottom: 425px;
			}
			&[data-item='6'] {
				transform: translateX(-50%) translateX(-230px);
				bottom: 290px;
			}
		}
	}
}

@media screen and (max-width: 1440px) {
	.road-map {
		&__wrapper {
			& img[data-img='bg'] {
				width: 3192px;
				height: 1544px;
				transform: translateX(-50%) translateX(-15px);
			}
		}
		&__road {
			&-wrapper {
				padding-top: 167px;
				padding-bottom: 329px;
			}
			& svg[data-img='road'] {
				display: none;
			}
			& svg[data-img='road-1440'] {
				display: block;
				max-width: 100%;
				height: auto;
			}
		}
		&__item {
			&-title {
				margin-bottom: 4px;
				& + p {
					font-size: 1.4rem;
					line-height: calc(22 / 14);
					margin-bottom: 6px;
				}
			}
			&-img {
				margin: 16px 0;
			}
			& p {
				font-size: 1.4rem;
				line-height: calc(22 / 14);
			}
			&[data-item='1'] {
				top: 5px;
				& img {
					transform: translateX(-19px);
				}
			}
			&[data-item='2'] {
				transform: translateX(-50%) translateX(-61px);
			}
			&[data-item='3'] {
				left: 50%;
				right: auto;
				top: 310px;
				transform: translateX(-50%) translateX(410px);
				& img {
					max-width: 40px;
				}
			}
			&[data-item='4'] {
				bottom: 310px;
				transform: translateX(-50%) translateX(486px);
			}
			&[data-item='5'] {
				bottom: 385px;
				transform: translateX(-50%) translateX(143px);
			}
			&[data-item='6'] {
				bottom: 305px;
				transform: translateX(-50%) translateX(-210px);
				& img {
					max-width: 40px;
				}
			}
			&[data-item='7'] {
				bottom: 144px;
				& img {
					transform: translateX(-19px);
				}
			}
		}
	}
}

@media screen and (max-width: 1300px) {
	.road-map {
		&__item {
			&[data-item='3'] {
				top: 310px;
				transform: translateX(-50%) translateX(380px);
			}
			&[data-item='4'] {
				bottom: 275px;
				transform: translateX(-50%) translateX(440px);
			}
		}
	}
}

@media screen and (max-width: 1200px) {
	.road-map {
		&__wrapper {
			padding-top: 88px;
			& img[data-img='bg'] {
				width: 3192px;
				height: 1544px;
				transform: translateX(-50%) translateX(250px);
			}
		}
		&__title {
			margin-bottom: 48px;
		}
		&__road {
			&-wrapper {
				padding-top: 0;
				padding-bottom: 81px;
			}
			& svg[data-img='road-1440'] {
				display: none;
			}
			& svg[data-img='road-1200'] {
				display: block;
				position: absolute;
				top: 0;
				left: 50%;
				transform: translateX(-50%) translateX(-200px);
			}
		}
		&__item {
			margin: 0 auto;
			z-index: 1;
			margin-bottom: 30px;
			&-img {
				margin: 0;
				position: absolute;
				right: calc(100% + 45px);
			}
			&[data-item='7'] {
				margin-bottom: 0;
			}
		}
		&__item[data-item='1'],
		&__item[data-item='2'],
		&__item[data-item='3'],
		&__item[data-item='4'],
		&__item[data-item='5'],
		&__item[data-item='6'],
		&__item[data-item='7'] {
			position: relative;
			top: auto;
			left: auto;
			right: auto;
			bottom: auto;
			transform: translateX(52px);
			& img {
				max-width: 40px;
				transform: none;
			}
		}
		&__item[data-item='2'] {
			& .road-map__item-img {
				right: calc(100% + 30px);
			}
		}
		&__item[data-item='3'] {
			& .road-map__item-img {
				right: calc(100% + 55px);
			}
		}
		&__item[data-item='4'] {
			& .road-map__item-img {
				right: calc(100% + 35px);
			}
		}
		&__item[data-item='6'] {
			& img {
				max-width: 30px;
			}
			& .road-map__item-img {
				right: calc(100% + 55px);
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.road-map {
		&__wrapper {
			& img[data-img='bg'] {
				top: -15px;
			}
		}
		&__item {
			max-width: 320px;
			&-title {
				margin-bottom: 6px;
			}
			& p {
				font-size: 1.2rem;
				line-height: calc(20 / 12);
			}
		}
		&__road {
			& svg[data-img='road-1200'] {
				height: 920px;
				transform: translateX(-50%) translateX(-175px);
			}
		}
		&__item[data-item='2'] {
			& .road-map__item-img {
				right: calc(100% + 35px);
			}
		}
		&__item[data-item='6'] {
			& .road-map__item-img {
				right: calc(100% + 60px);
			}
		}
		&__item[data-item='7'] {
			& .road-map__item-img {
				right: calc(100% + 35px);
			}
		}
	}
}

@media screen and (max-width: 576px) {
	.road-map {
		&__wrapper {
			& img[data-img='bg'] {
				transform: translateX(-50%) translateX(470px);
			}
		}
		&__road svg[data-img='road-1200'] {
			display: none;
		}
		&__road svg[data-img='road-576'] {
			display: block;
			position: absolute;
			top: 25px;
			left: 50%;
			transform: translateX(-50%) translateX(-150px);
		}
		&__item {
			margin-bottom: 40px;
			width: 272px;
			max-width: 81.38%;
		}
		&__item[data-item='1'],
		&__item[data-item='2'],
		&__item[data-item='3'],
		&__item[data-item='4'],
		&__item[data-item='5'],
		&__item[data-item='6'],
		&__item[data-item='7'] {
			transform: translateX(27px);
			& .road-map__item-img {
				right: calc(100% + 16px);
				& img {
					max-width: 32px;
				}
			}
		}
		&__item[data-item='2'] {
			& .road-map__item-img {
				right: calc(100% + 10px);
			}
		}
		&__item[data-item='3'] {
			& .road-map__item-img {
				right: calc(100% + 26px);
			}
		}
		&__item[data-item='4'] {
			& .road-map__item-img {
				right: calc(100% + 10px);
			}
		}
		&__item[data-item='6'] {
			& .road-map__item-img {
				right: calc(100% + 30px);
			}
		}
	}
}

@media screen and (max-width: 365px) {
	.road-map {
		&__road {
			& svg[data-img='road-576'] {
				transform: none;
				left: 0;
			}
		}
	}
}
