@import '../variables';
.space {
	& .swiper {
		&-container {
			display: none;
		}
	}
	&-swiper {
		&__navigation {
			display: flex;
			justify-content: space-between;
			padding: 0 24px;
			margin-top: 32px;
		}
		&__prev,
		&__next {
			width: 123px;
			height: 12px;
			border: none;
			background: transparent;
			display: grid;
			place-items: center;
			&:hover svg path {
				fill: $orange-200;
			}
			&:active svg path {
				fill: $orange-900;
			}
		}
	}
	&-wrapper {
		padding: 168px 0;
		padding-bottom: 144px;
		position: relative;
		& svg {
			position: absolute;
			pointer-events: none;
		}
		& svg[data-img='blue-1'] {
			left: 50%;
			top: 48px;
			transform: translateX(-50%) translateX(-435px);
			filter: blur(6px);
		}
		& svg[data-img='blue-2'] {
			right: 50%;
			bottom: 136px;
			transform: translateX(50%) translateX(770px);
		}
		& svg[data-img='green-1'] {
			left: 50%;
			top: 415px;
			transform: translateX(-50%) translateX(-870px);
		}
		& svg[data-img='red-1'] {
			right: 50%;
			top: 302px;
			transform: translateX(50%) translateX(830px);
		}
		& svg[data-img='red-2'] {
			right: 50%;
			top: 388px;
			transform: translateX(50%) translateX(700px) rotate(-60deg);
			filter: blur(9px) drop-shadow(0px 0px 74px rgba(254, 172, 65, 0.35));
		}
		& svg[data-img='yellow-1'] {
			left: 50%;
			bottom: 251px;
			transform: translateX(-50%) translateX(-710px);
		}
	}
	&-note {
		max-width: 1141px;
		margin: 0 auto;
		margin-bottom: 64px;
		&__title {
			margin-bottom: 24px;
		}
		&__subtitle {
			margin-bottom: 40px;
		}
		& p {
			text-align: center;
			font-size: 1.8rem;
			line-height: calc(28 / 18);
		}
	}
	&-items {
		display: flex;
		align-items: stretch;
		gap: 24px;
		margin-bottom: 104px;
		&__item {
			width: calc(((4 / 12) * 100%) - 16px);
			padding: 40px;
			border-radius: 20px;
			border: 2px solid #ffffff1a;
			background: radial-gradient(57.44% 57.44% at 50% 50%, #372181 0%, #251363 100%);
			box-shadow: 0px 0px 30px rgba(69, 48, 143, 0.6);
			& img {
				max-width: 100%;
				margin-bottom: 32px;
				border-radius: 20px;
				filter: drop-shadow(0px 0px 30px #191135);
			}
			&-title {
				margin-bottom: 8px;
			}
			& p {
				font-size: 1.6rem;
				line-height: calc(24 / 16);
			}
		}
	}
	&-subnote {
		max-width: 1141px;
		margin: 0 auto;
		&__title {
			margin-bottom: 16px;
		}
		&__list {
			list-style: none;
			display: flex;
			flex-direction: column;
			&-title {
				font-size: 1.8rem;
				line-height: calc(28 / 18);
				font-weight: 400;
			}
			&-item {
				font-size: 1.8rem;
				line-height: calc(28 / 18);
				font-weight: 400;
				& span {
					position: relative;
					padding-left: 10px;
					&::before {
						content: '-';
						position: absolute;
						left: 0;
						top: 0;
						height: 1em;
					}
				}
			}
		}
		&__text {
			text-align: center;
		}
	}
}

@media screen and (max-width: 1440px) {
	.space {
		&-wrapper {
			padding: 144px 0;
			& svg[data-img='blue-1'] {
				top: 58px;
				transform: translateX(-50%) translateX(-340px);
				width: 76px;
				height: 120px;
			}
			& svg[data-img='blue-2'] {
				bottom: 84px;
				transform: translateX(50%) translateX(550px);
				width: 80px;
			}
			& svg[data-img='green-1'] {
				top: 256px;
				transform: translateX(-50%) translateX(-609px);
			}
			& svg[data-img='red-1'] {
				top: 142px;
				transform: translateX(50%) translateX(590px);
				height: 80px;
			}
			& svg[data-img='red-2'] {
				top: 219px;
				width: 40px;
				transform: translateX(50%) translateX(476px) rotate(-60deg);
				filter: blur(9px) drop-shadow(0px 0px 74px rgba(254, 172, 65, 0.35));
			}
			& svg[data-img='yellow-1'] {
				bottom: 210px;
				transform: translateX(-50%) translateX(-515px);
				width: 78px;
			}
		}
		&-note {
			max-width: 1036px;
			margin-bottom: 48px;
			&__title {
				margin-bottom: 16px;
			}
			&__subtitle {
				margin-bottom: 32px;
			}
			& p {
				font-size: 1.4rem;
				line-height: calc(22 / 14);
			}
		}
		&-items {
			margin-bottom: 80px;
			&__item {
				padding: 32px;
				padding-bottom: 24px;
				& img {
					margin-bottom: 16px;
				}
				&-title {
					margin-bottom: 6px;
				}
				& p {
					font-size: 1.2rem;
					line-height: calc(20 / 12);
				}
			}
		}
		&-subnote {
			&__list {
				&-title {
					font-size: 1.4rem;
					line-height: calc(22 / 14);
				}
				&-item {
					font-size: 1.4rem;
					line-height: calc(22 / 14);
				}
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.space {
		& .swiper {
			&-slide {
				height: 100%;
			}
			&-wrapper {
				padding-top: 20px;
				align-items: center;
			}
			&-container {
				display: block;
				max-width: 450px;
				margin: 0 auto;
				padding: 0 20px;
			}
		}
		& .container {
			padding: 0;
		}
		&-wrapper {
			padding: 90px 0;
			padding-bottom: 88px;
			& svg[data-img='blue-1'] {
				top: 30px;
				transform: translateX(-50%) translateX(-85px);
				width: 30px;
				height: 60px;
				filter: blur(4px);
			}
			& svg[data-img='blue-2'] {
				top: auto;
				bottom: 220px;
				transform: translateX(50%) translateX(-115px);
				width: 45px;
			}
			& svg[data-img='green-1'] {
				top: 105px;
				width: 32px;
				z-index: 2;
				transform: translateX(-50%) translateX(-228px);
			}
			& svg[data-img='red-1'] {
				top: 60px;
				transform: translateX(50%) translateX(246px);
				height: 45px;
			}
			& svg[data-img='red-2'] {
				top: auto;
				bottom: 260px;
				width: 40px;
				transform: translateX(50%) translateX(130px) rotate(-60deg);
				filter: blur(6px) drop-shadow(0px 0px 74px rgba(254, 172, 65, 0.35));
			}
			& svg[data-img='yellow-1'] {
				display: none;
			}
		}
		&-note {
			padding: 0 20px;
			margin-bottom: 20px;
			z-index: 1;
			position: relative;
			&__title {
				margin-bottom: 14px;
			}
			&__subtitle {
				margin-bottom: 16px;
			}
			& p {
				font-size: 1.2rem;
				line-height: calc(18 / 12);
			}
		}
		&-items {
			display: none;
			&__item {
				max-width: 100%;
				width: auto;
				padding: 24px;
				padding-bottom: 18px;
				&-title {
					margin-bottom: 8px;
				}
			}
		}
		&-subnote {
			padding: 0 20px;
			max-width: 550px;
			margin-top: 90px;
			&__list-title,
			&__list-item {
				display: block;
				font-size: 1.2rem;
				line-height: calc(20 / 12);
				margin-bottom: 8px;
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.space {
		& .swiper-container {
			padding: 0 24px;
		}
		&-wrapper {
			padding-bottom: 40px;
			& svg[data-img='red-2'] {
				bottom: 220px;
				width: 40px;
				transform: translateX(50%) translateX(130px) rotate(-60deg);
				filter: blur(6px) drop-shadow(0px 0px 74px rgba(254, 172, 65, 0.35));
			}
			& svg[data-img='blue-2'] {
				top: auto;
				bottom: 170px;
				transform: translateX(50%) translateX(-115px);
				width: 45px;
			}
		}
		&-swiper {
			&__navigation {
				padding: 0;
			}
			&__prev,
			&__next {
				max-width: 100px;
				& svg {
					max-width: 100px;
				}
			}
		}
	}
}

@media screen and (max-width: 576px) {
	.space {
		&-note {
			padding: 0 24px;
		}
		&-subnote {
			max-width: 375px;
			padding: 0 24px;
			&__text {
				text-align: left;
			}
			&__list {
				&-item span {
					padding-left: 0;
				}
				&-item span::before {
					content: attr(data-item);
					position: relative;
					margin-right: 4px;
				}
			}
		}
		&-wrapper {
			& svg[data-img='blue-2'] {
				bottom: 210px;
				transform: translateX(50%) translateX(-115px);
				width: 45px;
			}
			& svg[data-img='green-1'] {
				left: 12px;
				top: 120px;
				transform: none;
			}
			& svg[data-img='red-1'] {
				transform: none;
				right: 31px;
				top: 20px;
			}
			& svg[data-img='red-2'] {
				top: auto;
				bottom: 260px;
				width: 30px;
				transform: translateX(50%) translateX(130px) rotate(-60deg);
			}
		}
	}
}

@media screen and (max-width: 409px) {
	.space {
		&-wrapper {
			& svg[data-img='green-1'] {
				left: 24px;
				transform: none;
				top: 85px;
				height: 32px;
			}
			& svg[data-img='red-1'] {
				transform: none;
				top: 60px;
				width: 40px;
			}
		}
	}
}
