@import '../variables';
.faq {
	&-wrapper {
		padding-bottom: 170px;
	}
	&-title {
		margin-bottom: 88px;
	}
	&-block {
		display: flex;
		justify-content: space-between;
	}
	&-img {
		margin-top: 48px;
		flex-grow: 1;
		max-width: 42.24%;
		& img {
			width: 100%;
			box-shadow: 0px 0px 30px 0px #45308f99;
			border-radius: 20px;
		}
	}
	&-accordeon {
		width: 49.31%;
		&__list {
			list-style: none;
		}
		&__item {
			margin-bottom: 29px;
			padding-bottom: 28px;
			border-bottom: 1px solid $white;
			position: relative;
			&:last-child {
				margin-bottom: 0;
			}
			&-title {
				padding-right: 30px;
			}
			&-btn {
				position: absolute;
				top: -3px;
				right: 0;
				& span {
					max-height: 40px;
					display: inline-block;
					font-size: 3.2rem;
					line-height: calc(40 / 32);
					font-weight: 600;
				}
				& span:last-child {
					display: none;
				}
			}
			& p {
				font-size: 1.6rem;
				line-height: calc(24 / 16);
				display: none;
			}
			&_active {
				& .faq-accordeon__item-btn {
					& span:first-child {
						display: none;
					}
					& span:last-child {
						display: inline;
					}
				}
				& .faq-accordeon__item-title {
					margin-bottom: 16px;
				}
				& p {
					display: block;
				}
			}
		}
	}
}

@media screen and (max-width: 1440px) {
	.faq {
		&-wrapper {
			padding-bottom: 144px;
		}
		&-title {
			margin-bottom: 55px;
		}
		&-accordeon {
			width: 49.03%;
			&__item {
				margin-bottom: 24px;
				& p {
					font-size: 1.4rem;
					line-height: calc(22 / 14);
				}
				&_active {
					&.faq-accordeon__item {
						padding-bottom: 24px;
					}
					& .faq-accordeon__item-title {
						margin-bottom: 18px;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 1100px) {
	.faq {
		&-accordeon {
			width: 53%;
		}
	}
}

@media screen and (max-width: 991px) {
	.faq {
		&-wrapper {
			padding-bottom: 88px;
		}
		&-block {
			flex-direction: column;
		}
		&-title {
			margin-bottom: 39px;
		}
		&-accordeon {
			width: auto;
		}
		&-img {
			align-self: center;
			width: 100%;
			max-width: 500px;
		}
	}
}

@media screen and (max-width: 767px) {
	.faq {
		&-accordeon {
			&__item {
				padding-bottom: 24px;
				&_active {
					& .faq-accordeon__item-title {
						margin-bottom: 8px;
					}
				}
				& p {
					font-size: 1.2rem;
					line-height: calc(20 / 12);
				}
			}
		}
	}
}
