.home {
	width: 100%;
	overflow: hidden;
	background: linear-gradient(180deg, #23115c -0.72%, #23115c 100%);
	&-main {
		position: relative;
		overflow: hidden;
		&__wrapper {
			position: relative;
			& img[data-img='pixelMonkey'] {
				position: absolute;
				pointer-events: none;
				bottom: -314px;
				left: -149px;
				z-index: 1;
			}
			& img[data-img='monkeys'] {
				position: absolute;
				pointer-events: none;
				bottom: 0;
				right: -73px;
			}
			& img[data-img='black-monkey'] {
				position: absolute;
				pointer-events: none;
				bottom: 0;
				right: -2px;
				display: none;
			}
		}
	}
}

@media screen and (max-width: 1440px) {
	.home-main {
		&__wrapper {
			& img[data-img='pixelMonkey'] {
				width: 622px;
				height: 728px;
				left: -93px;
				bottom: -255px;
			}
			& img[data-img='monkeys'] {
				width: 570px;
				height: 420px;
				right: -102px;
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.home-main {
		&__wrapper {
			& img[data-img='monkeys'] {
				width: 450px;
				height: 331px;
			}
			& img[data-img='pixelMonkey'] {
				width: 512px;
				height: 600px;
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.home-main {
		&__wrapper {
			& img[data-img='monkeys'] {
				display: none;
			}
			& img[data-img='pixelMonkey'] {
				height: 382px;
				width: 333px;
				left: -30px;
				bottom: -129px;
			}
			& img[data-img='black-monkey'] {
				display: block;
				min-width: 250px;
			}
		}
	}
}

@media screen and (max-width: 576px) {
	.home-main {
		&__wrapper {
			& img[data-img='pixelMonkey'] {
				left: -81px;
			}
			& img[data-img='black-monkey'] {
				min-width: auto;
			}
		}
	}
}
