@import '../variables';
.team {
	&-swiper {
		&__navigation {
			justify-content: space-between;
			padding: 0 24px;
			margin-top: 32px;
			display: none;
		}
		&__prev,
		&__next {
			width: 123px;
			height: 12px;
			border: none;
			background: transparent;
			display: grid;
			place-items: center;
			&:hover svg path {
				fill: $orange-200;
			}
			&:active svg path {
				fill: $orange-900;
			}
		}
	}
	&-wrapper {
		position: relative;
		padding: 168px 0;
		& svg[data-img='blue-1'] {
			position: absolute;
			pointer-events: none;
			top: 84px;
			right: 50%;
			transform: translateX(50%) translateX(678px);
			filter: drop-shadow(0px 0px 20px rgba(8, 57, 130, 0.35));
		}
		& svg[data-img='red-1'] {
			position: absolute;
			pointer-events: none;
			top: 173px;
			right: 50%;
			transform: translateX(50%) translateX(-648px);
			filter: blur(7px) drop-shadow(0px 0px 34px rgba(254, 172, 65, 0.35));
		}
	}
	&-title {
		margin-bottom: 72px;
	}
	&-block {
		& .swiper-container {
			overflow: visible;
		}
		& .swiper-wrapper {
			display: flex;
			flex-wrap: wrap;
			align-items: stretch;
			max-width: 100%;
		}
		& .swiper-slide {
			max-width: calc(((3 / 12) * 100%) - 18px);
			margin-bottom: 24px;
			margin-right: 24px;
			height: auto;
			&:nth-child(4n) {
				margin-right: 0;
			}
			&:nth-child(n + 5) {
				margin-bottom: 0;
			}
		}
	}
	&-member {
		height: 100%;
		width: 100%;
		border-radius: 20px;
		border: 2px solid #ffffff1a;
		box-shadow: 0px 0px 30px 0px #45308f99;
		background: radial-gradient(57.44% 57.44% at 50% 50%, #372181 0%, #251363 100%);
		padding: 27px 28px;
		padding-bottom: 24px;
		&__img {
			margin-bottom: 24px;
			& img {
				max-width: 100%;
				max-height: 355px;
				border-radius: 20px;
				filter: drop-shadow(0px 0px 30px $dark-900);
			}
		}
		&__title {
			margin-bottom: 6px;
		}
		&__social-link {
			display: flex;
			align-items: center;
			gap: 8px;
			margin-bottom: 6px;
			& img {
				max-height: 18px;
				margin: auto 0;
			}
			& span {
				font-size: 1.4rem;
				line-height: calc(22 / 14);
				color: $white;
			}
			&:hover span {
				color: $orange-300;
			}
			&:active span {
				color: $orange-800;
			}
		}
		& p {
			font-size: 1.6rem;
			line-height: calc(24 / 16);
			color: rgba($white, 0.5);
		}
	}
}

@media screen and (max-width: 1440px) {
	.team {
		&-wrapper {
			padding: 144px 0;
			& svg[data-img='blue-1'] {
				top: 64px;
				width: 42px;
				transform: translateX(50%) translateX(490px);
			}
			& svg[data-img='red-1'] {
				top: 140px;
				width: 60px;
				transform: translateX(50%) translateX(-460px);
			}
		}
		&-title {
			margin-bottom: 48px;
		}
		&-member {
			padding: 24px;
			&__img {
				margin-bottom: 15px;
			}
			&__title {
				margin-bottom: 2px;
			}
			&__social-link {
				margin-bottom: 4px;
				gap: 10px;
				& img {
					max-height: 14px;
				}
				& span {
					font-size: 1.2rem;
					line-height: calc(20 / 12);
				}
			}
			& p {
				font-size: 1.4rem;
				line-height: calc(22 / 14);
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.team {
		&-wrapper {
			padding: 88px 0;
			padding-bottom: 142px;
			& svg[data-img='blue-1'] {
				top: 21px;
				width: 40px;
				transform: translateX(50%) translateX(125px);
			}
			& svg[data-img='red-1'] {
				top: auto;
				bottom: 30px;
				width: 66px;
				transform: translateX(50%) translateX(-120px);
			}
		}
		&-swiper {
			&__navigation {
				display: flex;
			}
		}
		&-title {
			margin-bottom: 0;
		}
		& .swiper-container {
			padding-top: 32px;
			overflow: hidden;
			max-width: 450px;
		}
		& .swiper-wrapper {
			flex-wrap: nowrap;
			// align-items: stretch;
			max-width: none;
		}
		& .swiper-slide {
			max-width: none;
			margin: 0;
		}
		&-member {
			&__img {
				& img {
					max-height: none;
					width: 100%;
				}
			}
		}
	}
}

@media screen and (max-width: 576px) {
	.team {
		&-swiper {
			&__navigation {
				padding: 0;
			}
			&__prev,
			&__next {
				max-width: 100px;
				& svg {
					max-width: 100px;
				}
			}
		}
		&-wrapper {
			& svg[data-img='blue-1'] {
				top: 15px;
				width: 30px;
			}
		}
		& .swiper-container {
			overflow: visible;
		}
		&-member {
			& p {
				line-height: calc(18 / 12);
			}
		}
	}
}
