@import '../variables';
.about {
	&-wrapper {
		padding-bottom: 184px;
	}
	&-title {
		margin-bottom: 56px;
	}
	&-block {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		gap: 24px;
		&__item {
			padding: 30px 22px;
			border-radius: 20px;
			width: calc(((3 / 12) * 100%) - 18px);
			border: 2px solid #ffffff1a;
			background: radial-gradient(57.44% 57.44% at 50% 50%, #372181 0%, #251363 100%);
			box-shadow: 0px 0px 30px rgba(69, 48, 143, 0.6);
			&-title {
				margin-bottom: 8px;
			}
			& p {
				font-size: 1.6rem;
				line-height: calc(24 / 16);
			}
		}
	}
}

@media screen and (max-width: 1440px) {
	.about {
		&-wrapper {
			padding-bottom: 144px;
		}
		&-title {
			margin-bottom: 40px;
		}
		&-block {
			&__item {
				padding: 17px 18px;
				&-title {
					margin-bottom: 4px;
				}
				& p {
					font-size: 1.4rem;
					line-height: calc(20 / 14);
				}
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.about {
		&-wrapper {
			padding-bottom: 72px;
		}
		&-block {
			gap: 16px;
			&__item {
				width: calc(((3 / 12) * 100%) - 12px);
				padding: 16px;
				&-title {
					margin-bottom: 4px;
				}
				& p {
					font-size: 1.2rem;
					line-height: calc(18 / 12);
				}
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.about {
		&-block {
			row-gap: 16px;
			column-gap: 15px;
			&__item {
				width: calc(((6 / 12) * 100%) - 7.5px);
			}
		}
	}
}

@media screen and (max-width: 576px) {
	.about {
		&-title {
			display: none;
		}
	}
}
