.features {
	&-wrapper {
		padding-top: 168px;
	}
	&-title {
		margin-bottom: 64px;
	}
	&-block {
		display: flex;
		align-items: stretch;
		gap: 24px;
	}
	&-item {
		width: calc(((4 / 12) * 100%) - 16px);
		box-sizing: border-box;
		padding: 38px;
		padding-top: 50px;
		border-radius: 20px;
		border: 2px solid #ffffff1a;
		background: radial-gradient(57.44% 57.44% at 50% 50%, #372181 0%, #251363 100%);
		box-shadow: 0px 0px 30px rgba(69, 48, 143, 0.6);
		&:last-child {
			padding-right: 32px;
		}
		&__img {
			margin-bottom: 32px;
		}
		&__title {
			margin-bottom: 8px;
		}
		& p {
			font-size: 1.6rem;
			line-height: calc(24 / 16);
		}
	}
}

@media screen and (max-width: 1440px) {
	.features {
		&-wrapper {
			padding-top: 144px;
		}
		&-title {
			margin-bottom: 48px;
		}
		&-item {
			padding: 32px;
			& img {
				max-height: 67px;
			}
			&__img {
				margin-bottom: 30px;
			}
			&__title {
				margin-bottom: 6px;
			}
			& p {
				font-size: 1.4rem;
				line-height: calc(22 / 14);
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.features {
		&-wrapper {
			padding-top: 48px;
		}
		&-block {
			flex-direction: column;
			align-items: center;
			justify-content: stretch;
		}
		&-title {
			margin-bottom: 40px;
		}
		&-item {
			width: auto;
			max-width: 500px;
			padding: 24px;
			& img {
				max-height: 49px;
			}
			&__img {
				margin-bottom: 20px;
			}
			&__title {
				margin-bottom: 8px;
			}
			& p {
				font-size: 1.2rem;
				line-height: calc(20 / 12);
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.features {
		&-block {
			gap: 16px;
		}
	}
}
