@import '../variables';
.lady-apes {
	display: flex;
	width: 100%;
	flex-direction: column;
	align-items: center;
	& .swiper {
		&-container {
			width: calc(100% + 40px);
			left: 50%;
			position: relative;
			transform: translateX(-50%);
			z-index: 0;
			&[data-element='space-swiper'] {
				display: none;
			}
		}
		&-slide {
			max-width: 398px;
			& .lady-apes__item {
				transform: translateX(82px) scale(0.7487);
			}
			&-prev,
			&-duplicate-prev {
				& .lady-apes__item {
					transform: translateX(0);
				}
			}
			&-next,
			&-duplicate-next {
				& .lady-apes__item {
					transform: translateX(0);
				}
				& + .swiper-slide {
					& .lady-apes__item {
						transform: translateX(-82px) scale(0.7487);
					}
				}
			}
			&-active,
			&-duplicate-active {
				transform: none;
				& .lady-apes__item {
					transform: translateX(0) scale(1.246);
					&-shadow {
						opacity: 0;
					}
				}
			}
		}
	}
	&__wrapper {
		padding: 184px 0;
		padding-bottom: 60px;
		position: relative;
		& .swiper-container {
			padding-top: 113px;
			padding-bottom: 105px;
		}
		& svg[data-img='rock-1'] {
			position: absolute;
			pointer-events: none;
			left: 50%;
			top: 137px;
			transform: translateX(-50%) translateX(-610px);
		}
		& svg[data-img='red-1'] {
			position: absolute;
			pointer-events: none;
			right: 50%;
			top: 220px;
			transform: translateX(50%) translateX(530px);
			filter: blur(9px) drop-shadow(0px 0px 74px rgba(254, 172, 65, 0.35));
		}
		& svg[data-img='yellow-1'] {
			position: absolute;
			pointer-events: none;
			left: 50%;
			bottom: 24px;
			transform: translateX(-50%) translateX(-565px);
		}
		& svg[data-img='dark-1'] {
			position: absolute;
			pointer-events: none;
			right: 50%;
			bottom: -45px;
			transform: translateX(50%) translateX(483px);
		}
	}
	&__item {
		position: relative;
		& img {
			max-width: 100%;
			max-height: 100%;
			border-radius: 20px;
			filter: drop-shadow(0px 0px 30px rgba(69, 48, 143, 0.6));
		}
		&-shadow {
			border-radius: 20px;
			z-index: 1;
			position: absolute;
			background: $dark-500;
			opacity: 0.5;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
		}
	}
	&__navigation {
		display: flex;
		justify-content: space-between;
	}
	&__prev,
	&__next {
		position: relative;
		border: none;
		background: transparent;
		display: grid;
		place-items: center;
		&:hover svg path {
			fill: $orange-200;
		}
		&:active svg path {
			fill: $orange-900;
		}
	}
}

@media screen and (max-width: 1440px) {
	.lady-apes {
		& .swiper {
			&-slide {
				max-width: 287px;
			}
		}
		&__wrapper {
			padding: 144px 0;
			padding-bottom: 48px;
			& .swiper-container {
				padding: 83px 0;
			}
			& svg[data-img='rock-1'] {
				top: 93px;
				height: 96px;
				transform: translateX(-50%) translateX(-447px);
			}
			& svg[data-img='red-1'] {
				top: 170px;
				height: 60px;
				transform: translateX(50%) translateX(394px);
			}
			& svg[data-img='yellow-1'] {
				bottom: 19px;
				height: 100px;
				transform: translateX(-50%) translateX(-380px);
			}
			& svg[data-img='dark-1'] {
				bottom: 0;
				height: 110px;
				transform: translateX(50%) translateX(346px);
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.lady-apes {
		& .swiper {
			&-container {
				&[data-element='space-swiper'] {
					display: block;
				}
			}
		}
		&__wrapper {
			padding-top: 112px;
			& svg[data-img='rock-1'] {
				top: 93px;
				left: 24px;
				transform: none;
			}
			& svg[data-img='red-1'] {
				top: 102px;
				height: 60px;
				left: auto;
				right: 24px;
				transform: none;
			}
			& svg[data-img='yellow-1'] {
				display: none;
			}
			& svg[data-img='dark-1'] {
				display: none;
			}
		}
	}
}

@media screen and (max-width: 576px) {
	.lady-apes {
		& .container {
			max-width: 448px;
		}
		& .swiper-container {
			padding: 40px 0;
			padding-bottom: 30px;
			width: 100vw;
		}
		& .swiper-slide,
		& .swiper-slide-prev,
		& .swiper-slide-next,
		& .swiper-slide-active {
			max-width: 400px;
			width: calc(100vw - 48px);
			& .lady-apes__item {
				transform: none;
				& img {
					width: 100%;
				}
			}
		}
		&__prev,
		&__next {
			max-width: 100px;
			& svg {
				max-width: 100px;
			}
		}
		&__wrapper {
			& svg[data-img='rock-1'] {
				top: 24px;
				width: 40px;
				left: 24px;
			}
			& svg[data-img='red-1'] {
				top: 58px;
				height: 50px;
				right: 24px;
				filter: blur(8px) drop-shadow(0px 0px 20px rgba(254, 172, 65, 0.35));
			}
		}
	}
}
