@import '../variables';
.tokenomics {
	&-wrapper {
		padding: 168px 0;
		background: $dark-700;
	}
	&-title {
		margin-bottom: 82px;
	}
	&-modal {
		&__title {
			display: none;
			margin-bottom: 6px;
			text-align: center;
			font-size: 1.4rem;
			line-height: calc(20 / 14);
		}
	}
	&-scheme {
		margin: 0 auto;
		max-width: 1768px;
		padding: 0 20px;
		margin: 0 auto;
		position: relative;
		&__close-wrapper {
			width: 15px;
			height: 15px;
			position: absolute;
			bottom: calc(100% + 9px);
			right: 0;
			display: none;
		}
		&__close-btn {
			width: 15px;
			height: 15px;
			position: relative;
			border: none;
			background: transparent;
			& span {
				width: 15px;
				height: 1px;
				position: absolute;
				background: $white;
				border-radius: 10px;
				&:first-child {
					transform: translateX(-7px) rotate(45deg);
				}
				&:last-child {
					transform: translateX(-7px) rotate(-45deg);
				}
			}
		}
		& img {
			position: relative;
			left: 50%;
			transform: translateX(-50%);
			max-width: calc(100% + 60px);
		}
		& img[data-img='1440'] {
			display: none;
		}
		&__wrapper {
			position: relative;
		}
	}
	&-btn {
		&__wrapper {
			display: none;
		}
	}
}

@media screen and (max-width: 1440px) {
	.tokenomics {
		&-wrapper {
			padding: 144px 0;
		}
		&-title {
			margin-bottom: 48px;
		}
		&-scheme {
			& img {
				display: none;
			}
			& img[data-img='1440'] {
				display: block;
				position: relative;
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.tokenomics {
		&-wrapper {
			padding: 88px 0;
			padding-bottom: 104px;
		}
		&-scheme {
			& img[data-img='1440'] {
				max-width: calc(100%);
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.tokenomics {
		&-title {
			margin-bottom: 32px;
		}
		&-wrapper {
			position: relative;
		}
		&-modal {
			&__title {
				display: block;
			}
		}
		&-scheme {
			opacity: 0;
			pointer-events: none;
			position: fixed;
			top: 50%;
			left: 50%;
			transform: translateX(-50%) translateY(-50%);
			width: 100vw;
			padding: 0 24px;
			transition: opacity 0.4s;
			z-index: 30;
			&_open {
				opacity: 1;
				pointer-events: initial;
			}
			&__wrapper {
				border-radius: 10px;
				background: $dark-700;
				border: 2px solid $white;
				padding: 14px 5px;
			}
			&__close-wrapper {
				display: flex;
			}
		}
		&-btn {
			&__wrapper {
				display: block;
				text-align: center;
			}
		}
	}
}
