@import '../variables';
.investors {
	&-wrapper {
		padding: 198px 0;
		padding-bottom: 136px;
		position: relative;
		& svg[data-img='yellow-1'] {
			position: absolute;
			pointer-events: none;
			left: 50%;
			top: 120px;
			transform: translateX(-50%) translateX(-78px);
		}
		& svg[data-img='red-1'] {
			position: absolute;
			pointer-events: none;
			left: 50%;
			bottom: 67px;
			transform: translateX(-50%) translateX(-725px);
		}
		& svg[data-img='dark-1'] {
			position: absolute;
			pointer-events: none;
			right: 50%;
			bottom: -62px;
			transform: translateX(50%) translateX(774px);
			& path {
				box-shadow: (0px 0px 25px $dark-900);
			}
		}
	}
	&-block {
		display: flex;
		justify-content: space-between;
		align-items: center;
		gap: 94px;
		&__title {
			margin-bottom: 24px;
			text-align: left;
		}
		&__text {
			max-width: 49.3%;
			& p {
				font-size: 2rem;
				line-height: calc(28 / 20);
				margin-bottom: 10px;
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
		&__img {
			max-width: 42.24%;
			& img {
				max-width: 100%;
				border-radius: 20px;
				filter: drop-shadow(0px 0px 30px $dark-900);
			}
		}
	}
}

@media screen and (max-width: 1440px) {
	.investors {
		&-wrapper {
			padding: 144px 0;
			& svg[data-img='yellow-1'] {
				top: 84px;
				transform: translateX(-50%) translateX(-55px);
			}
			& svg[data-img='red-1'] {
				bottom: 17px;
				transform: translateX(-50%) translateX(-495px);
				width: 70px;
			}
			& svg[data-img='dark-1'] {
				width: 40px;
				bottom: -50px;
				transform: translateX(50%) translateX(538px);
			}
		}
		&-block {
			&__title {
				margin-bottom: 24px;
			}
			&__text {
				& p {
					font-size: 1.4rem;
					line-height: calc(22 / 14);
				}
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.investors {
		&-wrapper {
			padding: 88px 0;
			padding-bottom: 112px;
			& svg[data-img='yellow-1'] {
				top: 34px;
				width: 90px;
				transform: translateX(-50%) translateX(340px);
			}
			& svg[data-img='red-1'] {
				bottom: 30px;
				transform: none;
				left: 22px;
				width: 40px;
			}
			& svg[data-img='dark-1'] {
				width: 26px;
				bottom: -10px;
				left: auto;
				right: 23px;
				transform: none;
			}
		}
		&-block {
			gap: 0;
			flex-direction: column;
			&__title {
				text-align: center;
			}
			&__text {
				max-width: none;
				margin-bottom: 32px;
			}
			&__img {
				max-width: 500px;
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.investors {
		&-wrapper {
			& svg[data-img='red-1'] {
				left: 46px;
			}
			& svg[data-img='dark-1'] {
				right: 37px;
			}
		}
		&-block {
			&__text {
				& p {
					font-size: 1.2rem;
					line-height: calc(20 / 12);
					margin-bottom: 8px;
					&:last-child {
						margin-bottom: 0;
					}
				}
			}
		}
	}
}
