.up-btn {
	position: fixed;
	right: 40px;
	bottom: 100px;
	z-index: 10;
	display: grid;
	place-items: center;
	border-radius: 10px;
	width: 50px;
	height: 50px;
	border: 3px solid $white;
	background: radial-gradient(50% 50% at 50% 50%, #ffcc00 0%, #ff8337 100%);
	transition: 0.3s;
	&_hidden {
		opacity: 0;
		pointer-events: none;
	}
	&__wrapper {
	}
	&:hover {
		background: radial-gradient(75% 75% at 50% 50%, #ffa800 0%, #ff6400 100%);
	}
	&:active {
		background: radial-gradient(75% 75% at 50% 50%, #ca8500 0%, #c74e00 100%);
	}
}

@media screen and (max-width: 576px) {
	.up-btn {
		right: 24px;
		bottom: 40px;
	}
}
