@import '../variables';
.footer {
	background: $dark-500;
	&-wrapper {
		padding: 31px 0;
	}
	& .container {
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 20px;
	}
	&-nav {
		&__list {
			display: flex;
		}
		&__item {
			text-transform: uppercase;
			font-size: 1.6rem;
			line-height: calc(24 / 16);
			list-style: none;
			margin-right: 40px;
			&:last-child {
				margin-right: 0;
			}
			& a {
				color: $white;
			}
		}
	}
	&-socials {
		display: flex;
		& a {
			display: grid;
			place-items: center;
			width: 32px;
			height: 32px;
			margin-right: 32px;
			&:last-child {
				margin-right: 0;
			}
			&:hover svg path {
				fill: $orange-200;
			}
			&:active svg path {
				fill: $orange-900;
			}
		}
	}
}

@media screen and (max-width: 1440px) {
	.footer {
		&-wrapper {
			padding: 22px 0;
		}
		&-logo {
			& img {
				max-height: 44px;
			}
		}
		&-nav {
			&__item {
				font-size: 1.2rem;
				line-height: calc(20 / 12);
			}
		}
		&-socials {
			& a {
				margin-right: 16px;
				&:last-child {
					margin-right: 0;
				}
			}
			& a,
			& svg {
				height: 24px;
				width: 24px;
			}
			& svg[data-img='instagram'] {
				width: 16px;
				height: 16px;
			}
		}
	}
}

@media screen and (max-width: 1200px) {
	.footer {
		&-nav {
			&__item {
				margin-right: 20px;
				&:last-child {
					margin-right: 0;
				}
			}
		}
		&-socials {
			& a {
				margin-right: 10px;
				&:last-child {
					margin-right: 0;
				}
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.footer {
		& .container {
			flex-direction: column;
			gap: 0;
		}
		&-wrapper {
			padding: 40px 0;
		}
		&-logo {
			margin-bottom: 32px;
		}
		&-nav {
			width: 100%;
			margin-bottom: 24px;
			&__list {
				flex-direction: column;
				flex-wrap: wrap;
				max-height: 120px;
			}
			&__item {
				margin: 0;
				margin-bottom: 12px;
				&:nth-child(4n) {
					margin-bottom: 0;
				}
				&:nth-child(n + 5) {
					text-align: right;
				}
			}
		}
		&-socials {
			margin-right: 16px;
			&:last-child {
				margin-right: 0;
			}
		}
	}
}

@media screen and (max-width: 576px) {
	.footer {
		&-nav {
			&__item {
				&:nth-child(n + 5) {
					text-align: left;
				}
			}
		}
		&-socials {
			align-self: flex-start;
		}
	}
}
