@import '../variables';

.sandbox {
	&-wrapper {
		padding: 152px 0;
		padding-bottom: 156px;
		background: $dark-700;
	}
	&-block {
		display: flex;
		justify-content: space-between;
		&__note {
			max-width: calc(((6 / 12) * 100%) - 82px);
			align-self: flex-end;
			padding-bottom: 16px;
			& p {
				font-size: 1.8rem;
				line-height: calc(28 / 18);
				margin-bottom: 9px;
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
		&__video {
			max-width: calc(((6 / 12) * 100%) + 24px);
			display: grid;
			align-items: center;
			&-link {
				display: flex;
				position: relative;
				& img:first-child {
					border-radius: 20px;
					object-fit: contain;
					max-width: 100%;
				}
				& img[data-img='yt-btn'] {
					position: absolute;
					left: 50%;
					top: 50%;
					transform: translateX(-50%) translateY(-50%);
				}
			}
		}
	}
	&__logo {
		margin-bottom: 40px;
	}
	&__title {
		margin-bottom: 24px;
		text-align: left;
	}
}

@media screen and (max-width: 1440px) {
	.sandbox {
		&-wrapper {
			padding: 144px 0;
		}
		&__logo {
			margin-bottom: 16px;
			& svg {
				max-width: 187px;
			}
		}
		&__title {
			margin-bottom: 16px;
		}
		&-block {
			&__note {
				max-width: calc(((6 / 12) * 100%) - 44px);
				padding-bottom: 0;
				& p {
					font-size: 1.4rem;
					line-height: calc(22 / 14);
				}
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.sandbox {
		&-wrapper {
			padding: 60px 0;
			padding-bottom: 48px;
		}
		&-block {
			flex-direction: column;
			position: relative;
			&::before {
				content: '';
				position: absolute;
				top: 0;
				left: 50%;
				width: 8px;
				height: 8px;
				transform: translate(-50%) rotate(45deg);
				background: $lightblue-350;
			}
			&__note {
				max-width: none;
				margin-bottom: 32px;
			}
			&__video {
				max-width: none;
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.sandbox {
		&__title {
			margin-bottom: 12px;
		}
		&__logo {
			margin-bottom: 10px;
			& svg {
				max-width: 158px;
			}
		}
		&-block {
			&::before {
				width: 5px;
				height: 5px;
			}
			&__note {
				& p {
					font-size: 1.2rem;
					line-height: calc(20 / 12);
					margin-bottom: 0;
					&:last-child {
						display: none;
					}
				}
			}
			&__video-link img[data-img='yt-btn'] {
				width: 180px;
				height: 135px;
			}
		}
	}
}
