.container {
	max-width: 1768px;
	padding: 0 20px;
	margin: 0 auto;
}

@media screen and (max-width: 1440px) {
	.container {
		max-width: 1288px;
	}
}

@media screen and (max-width: 991px) {
	.container {
		max-width: 760px;
	}
}

@media screen and (max-width: 767px) {
	.container {
		max-width: 580px;
	}
}

@media screen and (max-width: 576px) {
	.container {
		padding: 0 24px;
		max-width: 588px;
	}
}
