.m-menu {
	position: fixed;
	background: $dark-500;
	z-index: 10;
	width: 100%;
	height: 100vh;
	left: 0;
	top: 0;
	transition: 0.4s;
	transform: translateX(-100%);
	overflow: scroll;
	&_active {
		transform: translateX(0);
	}
	&__wrapper {
		padding-bottom: 80px;
	}
	&__header {
		&-wrapper {
			padding-top: 36px;
			position: relative;
			display: grid;
			justify-items: center;
		}
	}
	&__navigation {
		&-list {
			list-style: none;
			padding-top: 56px;
			display: flex;
			flex-direction: column;
			align-items: center;
		}
		&-item {
			font-size: 2.4rem;
			text-transform: uppercase;
			line-height: calc(32 / 24);
			margin-bottom: 28px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	&-logo {
		& img {
			max-height: 45px;
		}
	}
	&__socials {
		display: flex;
		justify-content: center;
		padding-top: 64px;
		& a {
			display: grid;
			place-items: center;
			width: 35px;
			height: 35px;
			margin-right: 23px;
			&:last-child {
				margin-right: 0;
			}
			&:hover svg path {
				fill: $orange-200;
			}
			&:active svg path {
				fill: $orange-900;
			}
		}
		& svg {
			height: 35px;
			width: 35px;
		}
		& svg[data-img='instagram'] {
			width: 24px;
			height: 24px;
		}
	}
	&__button {
		border: none;
		background: transparent;
		position: relative;
		display: none;
		height: 20px;
		width: 30px;
		margin: auto 0;
		margin-left: auto;
		z-index: 20;
		transition: 0.4s;
		&_close {
			& span:first-child {
				transform: translateX(5px) translateY(9px) rotate(45deg);
				width: 21px;
			}
			& span:nth-child(2) {
				opacity: 0;
			}
			& span:last-child {
				transform: translateX(5px) translateY(-9px) rotate(-45deg);
				width: 21px;
			}
		}
		& span {
			position: absolute;
			display: inline-block;
			width: 100%;
			height: 2px;
			border-radius: 10px;
			left: 0;
			background: $white;
			transition: 0.5s;
			&:first-child {
				top: 0;
			}
			&:nth-child(2) {
				top: calc(50% - 1px);
			}
			&:last-child {
				bottom: 0;
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.m-menu {
		&__button {
			display: inline-block;
		}
	}
}

@media screen and (max-width: 767px) {
	.m-menu {
		&__button {
			&_close {
				transform: translateY(25px);
			}
		}
	}
}
