@import '../variables';
.header {
	position: fixed;
	width: 100vw;
	top: 0;
	left: 0;
	z-index: 999;
	&-wrapper {
		padding-top: 35px;
		position: relative;
	}
	& .container {
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 20px;
	}
	&-nav {
		&__list {
			display: flex;
			justify-content: space-between;
		}
		&__item {
			text-transform: uppercase;
			font-size: 1.6rem;
			line-height: calc(24 / 16);
			list-style: none;
			margin-right: 40px;
			&:last-child {
				margin-right: 0;
			}
			& a {
				color: $white;
			}
		}
	}
	&-socials {
		display: flex;
		& a {
			display: grid;
			place-items: center;
			width: 32px;
			height: 32px;
			margin-right: 32px;
			&:last-child {
				margin-right: 0;
			}
			&:hover svg path {
				fill: $orange-200;
			}
			&:active svg path {
				fill: $orange-900;
			}
		}
	}
}

@media screen and (max-width: 1700px) {
	.header {
		&-nav {
			&__item {
				margin-right: 15px;
				&:last-child {
					margin-right: 0;
				}
			}
		}
		&-socials {
			& a {
				margin-right: 9px;
				&:last-child {
					margin-right: 0;
				}
			}
		}
	}
}

@media screen and (max-width: 1440px) {
	.header {
		&-wrapper {
			padding-top: 32px;
		}
		&-nav {
			&__item {
				font-size: 1.2rem;
				line-height: calc(20 / 12);
				margin-right: 30px;
				&:last-child {
					margin-right: 0;
				}
			}
		}
		&-socials {
			& a {
				margin-right: 16px;
				&:last-child {
					margin-right: 0;
				}
			}
			& a,
			& svg {
				height: 24px;
				width: 24px;
			}
			& svg[data-img='instagram'] {
				width: 16px;
				height: 16px;
			}
		}
		&-logo {
			& img {
				max-height: 44px;
			}
		}
		&-connect {
			&__btn {
				font-size: 1.2rem;
				line-height: calc(20 / 12);
			}
		}
	}
}

@media screen and (max-width: 1260px) {
	.header {
		& .container {
			gap: 15px;
		}
		&-nav {
			&__item {
				margin-right: 20px;
				&:last-child {
					margin-right: 0;
				}
			}
		}
		&-socials {
			& a {
				margin-right: 10px;
				&:last-child {
					margin-right: 0;
				}
			}
		}
		&-connect {
			display: none;
		}
	}
}

@media screen and (max-width: 991px) {
	.header {
		position: fixed;
		width: 100%;
		&-nav {
			display: none;
		}
		&-socials {
			display: none;
		}
	}
}

@media screen and (max-width: 767px) {
	.header {
		&-wrapper {
			padding-top: 16px;
		}
		&-logo {
			display: flex;
			& img {
				max-height: 32px;
			}
		}
	}
}
