@import '../variables';
.about-us {
	&__wrapper {
		position: relative;
		padding: 168px 0;
		padding-bottom: 200px;
		background: $dark-700;
		& svg[data-img='blue-1'] {
			position: absolute;
			pointer-events: none;
			top: 89px;
			left: 50%;
			transform: translateX(-50%) translateX(-730px);
			filter: blur(5px);
		}
		& svg[data-img='red-1'] {
			position: absolute;
			pointer-events: none;
			top: 120px;
			left: 50%;
			transform: translateX(-50%) translateX(727px);
		}
	}
	&__title {
		margin-bottom: 64px;
	}
	&__block {
		display: flex;
		align-items: center;
		padding: 48px;
		padding-right: 56px;
		background: radial-gradient(57.44% 57.44% at 50% 50%, #2f1c6c 0%, #261365 100%);
		border-radius: 20px;
		border: 2px solid rgba(255, 255, 255, 0.1);
		box-shadow: 0px 0px 30px $dark-300;
		&-img {
			margin-right: 56px;
			max-width: 480px;
			& img {
				border-radius: 20px;
				filter: drop-shadow(0px 0px 30px #191135);
			}
		}
		&-title {
			margin-bottom: 24px;
		}
		&-text {
			& p {
				font-size: 2rem;
				line-height: calc(28 / 20);
				margin-bottom: 10px;
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}
}

@media screen and (max-width: 1440px) {
	.about-us {
		&__wrapper {
			padding: 144px 0;
			& svg[data-img='blue-1'] {
				top: 54px;
				width: 49px;
				transform: translateX(-50%) translateX(-530px);
			}
			& svg[data-img='red-1'] {
				top: 92px;
				width: 56px;
				transform: translateX(-50%) translateX(527px);
			}
		}
		&__title {
			margin-bottom: 48px;
		}
		&__block {
			padding: 32px;
			padding-right: 56px;
			&-img {
				flex-shrink: 0;
				max-width: 40.86%;
				& img {
					width: 100%;
					max-width: 474px;
				}
			}
			&-title {
				margin-bottom: 16px;
			}
			& p {
				font-size: 1.4rem;
				line-height: calc(22 / 14);
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.about-us {
		&__wrapper {
			padding: 88px 0;
			& svg[data-img='red-1'] {
				top: -25px;
				width: 48px;
				transform: none;
				left: auto;
				right: 36px;
			}
		}
		&__block {
			flex-direction: column;
			max-width: 500px;
			margin: 0 auto;
			padding: 26px;
			padding-bottom: 20px;
			&-img {
				margin: 0;
				margin-bottom: 16px;
				max-width: none;
			}
			&-title {
				margin-bottom: 9px;
			}
			& p {
				margin: 0;
				font-size: 1.2rem;
				line-height: calc(20 / 12);
			}
		}
	}
}

@media screen and (max-width: 576px) {
	.about-us {
		&__wrapper {
			& svg[data-img='red-1'] {
				width: 38px;
			}
		}
		&__title {
			margin-bottom: 32px;
		}
	}
}
